.menu-screen-tile {
    -ms-flex-align: center;
        align-items: center;
    color: #cccccc;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    position: absolute;
}

.menu-screen-tile-c1 {
    left: 0;
    width: 33%;
}

.menu-screen-tile-c2 {
    left: 33%;
    right: 33%;
}

.menu-screen-tile-c3 {
    width: 33%;
    right: 0;
}

.menu-screen-tile-r1 {
    top: 0;
    height: 33%;
}

.menu-screen-tile-r2 {
    top: 33%;
    bottom: 33%;
}

.menu-screen-tile-r3 {
    height: 33%;
    bottom: 0;
}

.menu-screen-tile-image {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    cursor: pointer;
    height: 35%;
    width: 75%;
}

.menu-screen-tile-label {
    font-size: 2vmax;
    font-weight: 400;
    margin-top: 2vh;
    text-align: center;
    text-transform: uppercase;
}


