body {
    background-color: black;
    font-family: Lato, sans-serif;
    margin: 0;
    padding: 0;
}

.full-page {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

.full-screen-icon {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    bottom: 2.5vmin;
    cursor: pointer;
    height: 10vmin;
    opacity: 0.2;
    position: absolute;
    right: 2.5vmin;
    width: 10vmin;
}

.back-icon {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    bottom: 2.5vmin;
    cursor: pointer;
    height: 10vmin;
    left: 2.5vmin;
    opacity: 0.2;
    position: absolute;
    width: 10vmin;
}
  
.fullscreen {
    background-color: transparent;
}


